// The splash page shown when first opening the site

.landing-page {
  @include background-image-standards("/images/pawel-czerwinski-lDtmU_ABtcU-unsplash.jpg");
  width: 100%;
  height: 100vh;
  display: flex;
  flex-flow: column;
  text-align: center;
  justify-content: center;



    h1 {
      margin-top: auto;
      margin-left: auto;
      margin-right: auto;
      line-height: normal;
      max-height: 128px;

      @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        margin-top: 0;
      }
    
    }

    .scroll {
      margin-top: auto;

      @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
        margin-bottom: auto;
      }
    }

  }
